import { cpf } from "../Forms/commonFields"

const loginForm = [
    {
        name: 'email',
        label: 'E-mail'
    },
    {
        name: 'password',
        label: 'Senha',
        type: 'password'
    },
]

const signUpForm = [
    {
        name: 'name',
        label: 'Nome Completo'
    },
    { ...cpf, name: 'cpf', field: 'cpf' },
    ...loginForm,
    {
        name: 'confirmPassword',
        label: 'Confirme sua senha'
    },
    {
        name: 'role',
        label: 'Perfil de usurio',
        options: [
            {
                optionLabel: 'Administrador',
                optionValue: 'admin'
            },
            {
                optionLabel: 'Delegatrio',
                optionValue: 'empresa'
            },
            {
                optionLabel: 'Tcnico Seinfra',
                optionValue: 'seinfra'
            },
        ]
    }
]

const userAuthForms = [
    { title: 'Fazer login', form: loginForm, buttonLabel: 'entrar', endPoint: '/auth/login' },
    { title: 'Cadastro de Usurio', form: signUpForm, buttonLabel: 'Cadastrar', endPoint: '/auth/signUp', toastMsg: 'Usurio cadastrado.' },
    { title: 'Recuperao de senha', form: signUpForm, buttonLabel: 'Recuperar senha', endPoint: '/auth/forgotPassword', toastMsg: 'Senha enviada para o e-mail registrado.' }
]

export default userAuthForms